@font-face {
  font-family: 'sectra-regular';
  src: url('./../../../public/fonts/GT-Sectra-Regular.woff') format('woff');
}

@font-face {
  font-family: 'walsheim-regular';
  src: url('./../../../public/fonts/gt-walsheim-medium.woff') format('woff');
}

h1,
h2,
h3,
h4,
h5,
h6,
h7,
.nav-element {
  font-family: 'sectra-regular';
}

p,
a,
div {
  font-family: 'walsheim-regular';
}

.button-container {
  background-color: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(10px);
  width: 40px;
  height: 40px;
  padding: 15px;
  border-radius: 15px;
  pointer-events: auto;
}
.button-container-default:hover {
  background-color: rgba(31, 31, 31, 0.9);
  cursor: pointer;
}
.button-container-disabled:hover {
  cursor: not-allowed;
}

.button-icon {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 100%;
  height: 100%;
}
.nav-container {
  position: absolute;
  top: 0px;
  left: 0px;
  height: auto;
  width: 100%;
  /* display: flex; */
  display: grid;
  grid-template-columns: auto min-content;
  grid-template-rows: 100%;
  grid-gap: 20px;
  /* justify-content: space-between; */
  /* background-color: yellow; */
  pointer-events: none;

  /* padding: 20px; */
}
.nav-menu-container {
  width: 100%;
  padding: 20px;
  max-height: 50vh;
}
.nav-menu {
  grid-column: 1/2;
  grid-row: 1/2;
  pointer-events: auto;
  overflow: hidden;
  height: auto;
  max-height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(40px);
  padding: 30px;
  border-radius: 15px;
}
.nav-scroll {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  grid-gap: 20px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  height: 100%;
  overflow-x: hidden;
}
.nav-element {
  color: rgb(255, 255, 255);
  font-size: 1.8em;
  word-wrap: break-word;
  /* background-color: blue; */
  max-width: 100%;
  text-decoration: none;
  /* text-align: right; */
}
.nav-element:hover {
  text-decoration: underline;
}

.nav-btn-container {
  grid-column: 2/3;
  grid-row: 1/2;
  width: fit-content;
  padding: 20px;

  /* padding: 20px; */
  /* background-color: blue; */
}

.ui-container {
  position: absolute;
  bottom: 0px;
  /* background-image: linear-gradient(to top, rgba(255, 255, 255, 255), rgba(255, 255, 255, 0)); */
  /* background-color: linear-gradient(45deg, red 0 50%, blue 50% 100%); */

  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,ffffff+80,ffffff+100 */
