  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.8) 30%,
    rgba(255, 255, 255, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
  width: 100%;
  /* height: fit-content; */
  /* height: auto; */
  /* max-height: 100vh; */
  /* max-height: 70vh; */
  /* height: fit-content; */
  padding: 0px 0px 20px 0px;
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 100%;
  /* flex-direction: column; */
  /* flex-flow: column-reverse; */
  /* grid-template-columns: 100%; */
  /* grid-template-rows: auto; */
  /* justify-content: center; */
  /* align-items: center; */
  justify-content: center;
  grid-gap: 10px;
  /* padding: 0px 0px 20px 0px; */
  /* background-color: purple; */
  pointer-events: none;
  /* justify-content: center; */
}

.bottom-ui {
  grid-row: 2/3;
  grid-column: 1/2;
  display: grid;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  grid-gap: 20px;
  width: fit-content;
  max-width: 100%;
  /* background-color: blue; */
}

.menu-container {
  /* grid-row: 3/4; */
  /* margin-left: auto; */
  /* margin-right: auto; */
  display: grid;
  width: fit-content;
  justify-content: center;
  /* background-color: yellow; */
}
.menu {
  display: flex;
  grid-gap: 20px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(40px);
  padding: 15px;
  pointer-events: all;
}

.menu-play-container {
  display: flex;
  grid-gap: 10px;
  /* background-color: yellow; */
}

.progress-bar-container {
  width: 100%;
  height: auto;
  justify-self: center;
  /* background-color: blue; */
}
.progress-bar {
  width: 100%;
  height: 6px;
  background-color: #666;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}
.progress-bar-progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 15%;
  height: 6px;
  background-color: rgb(174, 140, 236);
  background-color: rgb(180, 180, 180);
}
.progress-bar-viewpoints {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  /* background-color: blue; */
}
.progressbar-viewPoint {
  position: absolute;
  transform: translateX(-50%);
  top: 0px;
  width: 5px;
  height: 100%;
  /* background-color: #65a6df */
  background-color: #aaa;
}
/* .progress-bar-circle:hover {
  cursor: pointer;
} */

.popup-container {
  grid-row: 1/2;
  grid-column: 1/2;
  width: 100%;
  display: flex;
  color: rgb(255, 255, 255);
  height: 100%;
  /* background-color: yellow; */
  pointer-events: auto;
}

.popup {
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(40px);
  padding: 30px;
  margin: 50px;
  grid-gap: 15px;
  border-radius: 15px;
  height: 500px;
  max-height: 50vh;
  /* max-height: 60vh; */
  /* min-height: 25vh; */
  max-width: 1000px;
  justify-self: center;
  display: flex;
  flex-direction: column;
}
.popup-header {
  grid-column: 1/2;
  grid-row: 1/2;
  /* background-color: yellow; */
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.popup-close-btn {
  padding: 0px;
  margin: 0px;
}
.popup-headline {
  display: flex;
  flex-direction: column;
  grid-gap: 0px;
  width: auto;
}
.popup-headline * {
  margin: 5px;
  /* padding: 0px; */
}

.popup-content-container {
  grid-column: 1/2;
  grid-row: 2/3;
  overflow: hidden;
  height: 100%;
}
.popup-content {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  height: 100%;
  /* background-color: blue; */
}

.popup-text {
  font-size: 20px;
}

.transcript-container {
  grid-column: 1/2;
  grid-row: 1/2;
  width: 100%;
  display: flex;
  max-height: 140px;
  max-width: 1000px;
  /* background-color: yellow; */
  justify-self: center;
  justify-content: center;
  overflow: hidden;
  padding-top: 100px;
  display: none;
  /* padding: ; */
}
.transcript {
  /* padding: 20px 0px 20px 0px; */
  /* margin-top: 20px; */
  /* background-color: orange; */
  height: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  min-width: 40vw;
  max-width: 90vw;
  font-size: 20pt;
  /* display: flex; */
  /* justify-content: center; */
  /* padding: 100px 0px 100px 0px; */
  /* padding-top: 100px; */
  /* padding-bottom: 100px; */
  text-align: center;

  scrollbar-width: none;
  /* height: calc(100% - 1em); */
  /* background-color: blue; */
}
.transcript-text {
  font-size: 1.4em;
  margin: 0;
  padding: 0;
  margin-top: 3em;
  margin-bottom: 18%;
}

.transcript::-webkit-scrollbar {
  width: 0;
}

