.slide-in {
  animation-name: slideInAnimation;
  animation-duration: 0.2s;
  animation-timing-function: ease-in;
}

.show {
  display: inherit;
  animation-name: showAnimation;
  animation-duration: 0.2s;
  animation-timing-function: ease-in;
}
.hide {
  display: none;
}

.show-transcript {
  display: inherit;
  animation-name: showAnimationTranscript;
  animation-duration: 0.4s;
  animation-timing-function: ease-in;
  pointer-events: auto;
}

.hide-transcript {
  display: inherit;
  animation-name: hideAnimationTranscript;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

@keyframes slideInAnimation {
  from {
    transform: translateY(100px);
  }

  to {
    transform: translateY(0px);
  }
}

@keyframes showAnimation {
  from {
    opacity: 0%;
  }

  to {
    opacity: 100%;
  }
}
@keyframes showAnimationTranscript {
  from {
    opacity: 0%;
    height: 0%;
  }

  to {
    opacity: 100%;
    height: 100%;
  }
}
@keyframes hideAnimationTranscript {
  from {
    display: inherit;
    opacity: 100%;
    /* height: 100% */
  }

  to {
    /* height: 0%; */
    opacity: 0%;
    display: none;
    height: 0px;
    /* height: ; */
    padding-top: 0px;
    pointer-events: none;
  }
}

@media screen and (max-width: 1000px) {
  .button-container {
    width: 30px;
    height: 30px;
    padding: 12px;
  }

  .ui-container {
    padding: 0px 0px 15px 0px;
  }
  .nav-container {
    grid-gap: 15px;
  }

  .nav-menu-container {
    padding: 15px;
  }
  .nav-menu {
    padding: 30px;
  }

  .nav-btn-container {
    padding: 15px;
  }

  .bottom-ui {
    grid-gap: 10px;
  }

  .menu {
    display: flex;
    grid-gap: 15px;
    border-radius: 12px;
    padding: 10px;
  }

  .progress-bar {
    height: 5px;
  }

  .menu-play-container {
    display: flex;
    grid-gap: 5px;
  }

  .transcript-container {
    max-height: 140px;
    padding-top: 30px;
  }

  .transcript-text {
    font-size: 0.8em;
  }

  .popup p {
    font-size: 0.8em;
    line-height: 150%;
  }

  /* .popup-container { */
  /* height: fit-content; */
  /* max-height: calc(100% - 120px); */
  /* } */
  .popup {
    padding: 20px;
    margin: 20px;
  }
}
